import {
  AbstractControl as NgAbstractControl,
  FormArray as NgFormArray,
  FormControl  as NgFormControl,
  FormGroup as NgFormGroup,
  FormBuilder as NgFormBuilder
} from '@angular/forms';

export const AbstractControl = NgAbstractControl;
export const FormArray = NgFormArray;
export const FormControl = NgFormControl;
export const FormGroup = NgFormGroup;
export const FormBuilder =  NgFormBuilder;

export function toUntyped(control) {
  return control;
}



